import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import VolumeUpIcon from '@material-ui/icons/VolumeUp';
import { AccordionSummary } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Accordion from '@material-ui/core/Accordion';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogActions from '@material-ui/core/DialogActions';
import { useSelector } from 'react-redux';
import { selectUser } from 'features/auth/authSlice';
import AudioInput from '../AudioInput';

async function speak(text) {
  const speechSyn = new SpeechSynthesisUtterance(text);
  speechSyn.lang = 'en-US';
  return window.speechSynthesis.speak(speechSyn);
}

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  chip: {
    padding: '5px 20px 0px 20px',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  actions: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
  },
  audioInput: {
    alignSelf: 'flex-end',
  },
}));

export default function PhraseItem({ phrase, isUpdate, categories, handleOpen, handleDelete }) {
  const classes = useStyles();
  const user = useSelector(selectUser);
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const phraseCategories = categories.filter(
    (item) => phrase.categories && phrase.categories.includes(item.id),
  );
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <Accordion
      expanded={expanded === 'panel1'}
      onChange={handleChange('panel1')}
      className={classes.root}
    >
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1bh-content"
        id="panel1bh-header"
      >
        <div className={classes.heading}>
          {phraseCategories.length
            ? phraseCategories.map((item) => (
                <Chip key={item.id} label={item.name} color="primary" />
              ))
            : 'Word of the Day'}
        </div>
        <Typography className={classes.secondaryHeading}>{phrase.title}</Typography>
      </AccordionSummary>
      <AccordionDetails>
        {phrase.image && <img src={phrase.image} alt={phrase.title} width="360" height="240" />}
        <div className="content">
          <Typography>{phrase.meaning}</Typography>
        </div>
        <div className={classes.actions}>
          <CardActions>
            <Button size="small" type="button" onClick={() => speak(phrase.title)}>
              <VolumeUpIcon />
            </Button>
            {isUpdate && (
              <Button size="small" type="button" onClick={() => handleOpen(phrase)}>
                Edit
              </Button>
            )}
            {isUpdate && (
              <Button size="small" type="button" onClick={handleClickOpen}>
                Delete
              </Button>
            )}
          </CardActions>
          <div className={classes.audioInput}>
            <AudioInput phraseId={phrase.id} userId={user.uid} />
          </div>
        </div>
      </AccordionDetails>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Are you sure to remove this phrase?</DialogTitle>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button onClick={() => handleDelete(phrase)} color="secondary" autoFocus>
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </Accordion>
  );
}
