import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useForm, Controller } from 'react-hook-form';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { TextField } from '@material-ui/core';
import NameInput from '../NameInput';

const useStyles = makeStyles((theme) => ({
  form: {
    width: '100%',
    margin: theme.spacing(0, 0, 2),
  },
  input: {
    padding: theme.spacing(1, 0),
    margin: theme.spacing(0.2, 0, 0, 0),
  },
}));

export default function CategoryForm({ onSubmit, category }) {
  const classes = useStyles();
  const {
    handleSubmit,
    formState: { errors },
    control,
    setValue,
    reset,
  } = useForm();

  const submit = async (data) => {
    const defaultValue = {
      name: '',
      description: '',
    };
    onSubmit({ name: data.name, description: data.description ?? '' }, category);
    reset(defaultValue);
  };

  return (
    <form
      onSubmit={(e) => {
        e.stopPropagation();
        handleSubmit(submit)(e);
      }}
      className={classes.form}
    >
      <Grid container direction="column" justifyContent="center" alignItems="center" spacing={1}>
        <Grid container item xs={12} spacing={1} className={classes.input}>
          <Controller
            name="name"
            fullWidth
            control={control}
            rules={{ required: true }}
            defaultValue={category.name ?? ''}
            placeholder="Name"
            // eslint-disable-next-line no-unused-vars
            render={({ field: { ref, ...rest }, fieldState }) => (
              <NameInput
                {...rest}
                {...fieldState}
                error={errors.name}
                onChange={(e) => {
                  setValue('name', e.currentTarget.value, {
                    shouldValidate: true,
                  });
                }}
              />
            )}
          />
          {errors.title && <span>Title is required</span>}
        </Grid>
        <Grid container item xs={12} spacing={1} className={classes.input}>
          <Controller
            name="description"
            control={control}
            defaultValue={category.description ?? ''}
            render={({ field }) => <TextField {...field} fullWidth label="Description" />}
          />
        </Grid>
        <Grid container item xs={12} spacing={1} className={classes.input}>
          <Button type="submit" variant="contained" color="primary">
            Submit
          </Button>
        </Grid>
      </Grid>
    </form>
  );
}
