import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import { getCategories } from 'features/category/categoryService';
import { selectCategories } from 'features/category/categorySlice';
import { selectUser } from 'features/auth/authSlice';

import { removePhrase, getPhrases, updatePhrase } from '../../phraseService';
import PhraseModal from '../PhraseModal';
import PhraseItem from '../PhraseItem';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  list: {
    flexGrow: 1,
  },
  gridList: {},
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
}));

export default function AccordionPhraseList({ phrases, options }) {
  const user = useSelector(selectUser);
  const categories = useSelector(selectCategories);
  const [phrase, setPhrase] = useState();
  const classes = useStyles();
  const dispatch = useDispatch();

  const [open, setOpen] = useState(false);

  const handleOpen = (currentPhrase) => {
    setPhrase(currentPhrase);
    setOpen(true);
  };

  const handleDelete = async (currentPhrase) => {
    await dispatch(removePhrase(currentPhrase.id));
    return dispatch(getPhrases(options));
  };

  const handleClose = () => {
    setOpen(false);
  };

  const updatePhraseAction = async (data, currentPhrase) => {
    await dispatch(
      updatePhrase({
        ...currentPhrase,
        ...data,
      }),
    );
    setOpen(false);
    return dispatch(getPhrases(options));
  };
  useEffect(() => {
    dispatch(getPhrases(options));
    dispatch(
      getCategories({
        limit: 1000,
      }),
    );
  }, []);

  return (
    <div className={classes.root}>
      {phrases.map((phraseItem) => (
        <PhraseItem
          phrase={phraseItem}
          isUpdate={user && (user.uid === phraseItem.userId || !phraseItem.userId)}
          categories={categories}
          key={phraseItem.id}
          handleOpen={handleOpen}
          handleDelete={handleDelete}
        />
      ))}
      <PhraseModal
        open={open}
        handleClose={handleClose}
        phrase={phrase}
        updatePhrase={updatePhraseAction}
      />
    </div>
  );
}
