import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import AccordionPhraseList from 'features/phrase/components/PhraseList/list';
import { useDispatch, useSelector } from 'react-redux';
import { Pagination } from '@material-ui/lab';
import { selectOptions, selectPhrases, setOptions } from 'features/phrase/phraseSlice';
import { getPhrases } from 'features/phrase/phraseService';
import { TextField } from '@material-ui/core';
import debounce from 'lodash.debounce';

const useStyles = makeStyles((theme) => ({
  body: {
    padding: theme.spacing(1, 0, 0, 0),
  },
  form: {
    width: '100%',
    padding: theme.spacing(0, 0, 0, 2),
  },
  input: {
    padding: theme.spacing(1, 0),
    margin: theme.spacing(0.2, 0, 0, 0),
  },
}));

export default function MainPage() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const options = useSelector(selectOptions);
  const phrases = useSelector(selectPhrases);
  const handleChange = (_, value) => {
    const newOptions = {
      ...options,
      page: value,
    };
    dispatch(setOptions(newOptions));
  };

  const handleSearch = debounce((event) => {
    dispatch(setOptions({ ...options, query: event.target.value }));
  }, 500);

  useEffect(() => {
    dispatch(getPhrases(options));
  }, [options]);

  return (
    <Grid container className={classes.body}>
      <TextField id="search" label="Search" onChange={handleSearch} />
      <Pagination
        count={options.totalPage}
        page={options.page}
        onChange={handleChange}
        showFirstButton
        showLastButton
      />
      <AccordionPhraseList phrases={phrases} options={options} />
    </Grid>
  );
}
