import React from 'react';
import { useSelector } from 'react-redux';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { selectAllPhrases } from '../../phraseSlice';

export default function WordInput({ onChange, ...props }) {
  const phrases = useSelector(selectAllPhrases);
  const [options, setOptions] = React.useState([]);

  React.useEffect(() => {
    setOptions(phrases.map((option) => `${option.title}`));
  }, [phrases]);

  return (
    <Autocomplete
      {...props}
      fullWidth
      freeSolo
      onClose={(e) => onChange(e)}
      disableClearable
      id="phrase-input"
      options={options}
      renderInput={(params) => <TextField {...params} label="Title" />}
    />
  );
}
