import React, { useState, useRef, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Button, CardActionArea } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';

const useStyles = makeStyles((theme) => ({
  input: {
    width: '100%',
    margin: theme.spacing(0, 0, 2),
  },
  imgPreview: {
    width: '240px',
  },
}));

export default function ImageInput({ onChange }) {
  const classes = useStyles();
  const [image, setImage] = useState(null);
  const fileNameRef = useRef('unknown.png');
  const handleChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      fileNameRef.current = file;
      onChange(fileNameRef.current);
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onloadend = () => {
        setImage(reader.result);
      };
    }
  };

  const removeImage = () => {
    setImage(null);
    fileNameRef.current = null;
    onChange(null);
  };

  useEffect(() => {
    document.onpaste = (event) => {
      const { items } = event.clipboardData || event.originalEvent.clipboardData;
      for (let i = 0; i < items.length; i++) {
        const item = items[i];
        if (item.type.indexOf('image') !== -1) {
          const file = item.getAsFile();
          fileNameRef.current = file;
          onChange(fileNameRef.current);
          const reader = new FileReader();
          reader.onload = (e) => {
            setImage(e.target.result);
          };

          reader.readAsDataURL(file);
        }
      }
    };
  }, []);

  return (
    <div className={classes.input}>
      <Card className={classes.imgPreview}>
        {image && (
          <CardActionArea>
            <img src={image} id="file" width="240" height="180" alt="Preview" />
          </CardActionArea>
        )}
        <CardActions>
          {image && (
            <Button size="small" color="primary" onClick={removeImage}>
              Remove
            </Button>
          )}
          {!image && <input type="file" id="file" onChange={handleChange} />}
        </CardActions>
      </Card>
    </div>
  );
}
