import firebase, { fbStorage } from 'utils/firebase';

const handleUploadImage = (file, folder = 'images') => {
  const uploadTask = fbStorage.ref().child(`${folder}/${file.name}`).put(file.data);

  return new Promise((resolve, reject) => {
    uploadTask.on(
      firebase.storage.TaskEvent.STATE_CHANGED,
      () => {},
      (error) => {
        reject(error);
      },
      () => {
        uploadTask.snapshot.ref.getDownloadURL().then((url) => {
          resolve(url);
        });
      },
    );
  });
};

export const firebaseService = {
  handleUploadImage,
};

export default firebaseService;
