/* eslint-disable jsx-a11y/media-has-caption */
import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AudioReactRecorder, { RecordState } from 'audio-react-recorder';
import MicIcon from '@material-ui/icons/Mic';
import PauseCircleOutlineIcon from '@material-ui/icons/PauseCircleOutline';
import StopIcon from '@material-ui/icons/Stop';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';

import firebase, { fbStorage } from 'utils/firebase';

const useStyles = makeStyles(() => ({
  input: {
    width: '100%',
    display: 'flex',
  },
  audio: {
    height: '15px',
  },
  micBtn: {
    borderRadius: '50%',
    width: '40px',
    height: '40px',
  },
}));

export default function AudioInput({ userId, phraseId }) {
  const classes = useStyles();
  const [recordState, setRecordState] = useState(null);
  const [audio, setAudio] = useState(null);
  const [audioUrl, setAudioUrl] = useState();
  const handleStart = () => {
    setAudio(null);
    setRecordState(RecordState.START);
  };
  const handleStop = () => {
    setRecordState(RecordState.STOP);
  };
  const handlePause = () => {
    setRecordState(RecordState.PAUSE);
  };

  const onStop = (audioData) => {
    setAudio(audioData);
  };

  const uploadAudio = () => {
    const uploadTask = fbStorage.ref().child(`audios/${userId}/${phraseId}.wav`).put(audio.blob);

    uploadTask.on(
      firebase.storage.TaskEvent.STATE_CHANGED,
      () => {},
      (error) => {
        throw error;
      },
      () => {
        uploadTask.snapshot.ref.getDownloadURL().then((url) => {
          // eslint-disable-next-line no-console
          console.log('uploaded', url);
        });
      },
    );
  };

  useEffect(() => {
    fbStorage
      .ref()
      .child(`audios/${userId}/${phraseId}.wav`)
      .getDownloadURL()
      .then((url) => setAudioUrl(url))
      .catch(() => {});
  }, []);
  return (
    <div className={classes.input}>
      <AudioReactRecorder
        canvasWidth={recordState !== RecordState.STOP ? '100px' : '0px'}
        canvasHeight={recordState !== RecordState.STOP ? '20px' : '0px'}
        state={recordState}
        onStop={onStop}
      />
      {(audio || audioUrl) && (
        <audio controls className={classes.audio}>
          <source src={audio ? audio.url : audioUrl} />
        </audio>
      )}
      {(recordState === RecordState.STOP || !recordState) && (
        <button type="button" className="micBtn" onClick={handleStart}>
          <MicIcon />
        </button>
      )}
      {recordState === RecordState.START && (
        <button type="button" className="micBtn" onClick={handlePause}>
          <PauseCircleOutlineIcon />
        </button>
      )}
      {recordState === RecordState.PAUSE && (
        <button type="button" className="micBtn" onClick={handleStart}>
          <PlayArrowIcon />
        </button>
      )}
      {recordState !== RecordState.STOP && (
        <button type="button" className="micBtn" onClick={handleStop}>
          <StopIcon />
        </button>
      )}
      {audio && userId && phraseId && (
        <button type="button" className="micBtn" onClick={uploadAudio}>
          <CloudUploadIcon />
        </button>
      )}
    </div>
  );
}
