import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import AccordionPhraseList from 'features/phrase/components/PhraseList/list';
import { selectOptions, selectPhrases } from 'features/phrase/phraseSlice';
import AddPhrase from '../../components/AddPhrase';
import { addPhrase, getPhrases } from '../../phraseService';

const useStyles = makeStyles((theme) => ({
  body: {
    padding: theme.spacing(1, 0, 0, 0),
  },
  form: {
    width: '100%',
    padding: theme.spacing(0, 0, 0, 2),
  },
  input: {
    padding: theme.spacing(1, 0),
    margin: theme.spacing(0.2, 0, 0, 0),
  },
}));

export default function AddPage() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const addPhraseAction = async (data) => {
    dispatch(addPhrase(data));
    dispatch(getPhrases({ limit: 50, page: 1 }));
  };
  const options = useSelector(selectOptions);
  const phrases = useSelector(selectPhrases);

  return (
    <Grid container direction="row" alignItems="flex-start" className={classes.body}>
      <Grid container item xs={12} className={classes.form}>
        <AddPhrase addPhrase={addPhraseAction} />
      </Grid>
      <Grid container item xs={12} className={classes.form}>
        <AccordionPhraseList phrases={phrases} options={options} />
      </Grid>
    </Grid>
  );
}
