import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useSelector } from 'react-redux';
import { useForm, Controller } from 'react-hook-form';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import CategoryModal from 'features/category/components/CategoryModal';
import { selectCategories } from 'features/category/categorySlice';
import { selectUser } from 'features/auth/authSlice';
import { firebaseService } from 'services/firebase';
import WordInput from '../WordInput';
import ImageInput from '../ImageInput';

const useStyles = makeStyles((theme) => ({
  form: {
    width: '100%',
    margin: theme.spacing(0, 0, 2),
  },
  input: {
    padding: theme.spacing(1, 0),
    margin: theme.spacing(0.2, 0, 0, 0),
  },
  inputGroup: {
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(3),
    },
    display: 'flex',
    flexDirection: 'row',
  },
  dropdown: {
    width: 'calc(100% - 56px)',
  },
  addBtn: {
    width: '56px',
    marginTop: 0,
  },
}));

export default function AddPhrase({ addPhrase, phrase = {} }) {
  const classes = useStyles();
  const categories = useSelector(selectCategories);
  const user = useSelector(selectUser);
  const [open, setOpen] = useState(false);
  const [image, setImage] = useState(null);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const {
    handleSubmit,
    setValue,
    formState: { errors },
    control,
    reset,
  } = useForm();

  const onSubmit = async (data) => {
    const imageUrl =
      image &&
      (await firebaseService.handleUploadImage({
        data: image,
        name: `${data.title.trim().replace(/\s/g, '_')}.png`,
      }));
    addPhrase(
      {
        ...data,
        meaning: data.meaning || '',
        image: imageUrl || phrase.image || '',
        userId: user.uid,
      },
      phrase,
    );
    reset({
      title: '',
      meaning: phrase.meaning || '',
      image: phrase.image || '',
      categories: (phrase && phrase.categories) || [],
    });
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
      <Grid container direction="column" justifyContent="center" alignItems="center" spacing={1}>
        <Grid container item xs={12} spacing={1} className={classes.input}>
          <Controller
            name="title"
            fullWidth
            control={control}
            rules={{ required: true }}
            defaultValue={phrase.title || ''}
            placeholder="Phrase"
            // eslint-disable-next-line no-unused-vars
            render={({ field: { ref, ...rest }, fieldState }) => (
              <WordInput
                {...rest}
                {...fieldState}
                error={errors.title}
                onChange={(e) => {
                  setValue('title', e.currentTarget.value, {
                    shouldValidate: true,
                  });
                }}
              />
            )}
          />
          {errors.title && <span>Title is required</span>}
        </Grid>
        <Grid container item xs={12} spacing={1} className={classes.input}>
          <Controller
            name="meaning"
            control={control}
            defaultValue={phrase.meaning ?? ''}
            render={({ field }) => <TextField {...field} fullWidth label="Meaning" />}
          />
        </Grid>
        <Grid container item xs={12} spacing={1} className={classes.inputGroup}>
          <Controller
            name="categories"
            render={({ field }) => (
              <Autocomplete
                {...field}
                multiple
                id="categories-input"
                options={categories.map((category) => category.id)}
                getOptionLabel={(option) => {
                  const item = categories.find((category) => category.id === option);
                  return item && item.name;
                }}
                className={classes.dropdown}
                renderInput={(params) => (
                  <TextField {...params} label="Categories" placeholder="Categories" />
                )}
                onChange={(_, data) => field.onChange(data)}
              />
            )}
            control={control}
            defaultValue={phrase.categories || []}
          />
          <Fab color="primary" aria-label="add" className={classes.addBtn} onClick={handleOpen}>
            <AddIcon />
          </Fab>
        </Grid>
        <Grid container item xs={12} spacing={1} className={classes.input}>
          <ImageInput onChange={(value) => setImage(value)} />
        </Grid>

        <Grid container item xs={12} spacing={1} className={classes.input}>
          <Button type="submit" variant="contained" color="primary">
            {' '}
            Submit{' '}
          </Button>
        </Grid>
      </Grid>
      <CategoryModal open={open} handleClose={handleClose} />
    </form>
  );
}
